.modal-wrapper {
     position: fixed;
     bottom: 0;
     width: 100%;
     height: 100%;
     background-color: rgba(0, 0, 0, 0.2);
}

.modal-box {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 50%;
    height: 365px;
    background-color: rgb(255, 255, 255);
    border-radius: 5px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
    padding: 10px;
    -webkit-animation: modalBox 650ms ;
            animation: modalBox 650ms ;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}



.modal-header{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    border: none;
}

.modal-Title {
    height: 6vh;
}

.closeBtn {
    position: relative;
    font-size: 29px;
    bottom: 26px;
    cursor: pointer;
    color: red;
}
.modal-content {
    top: 30px;
    padding: 10px;
    height: 100px;
    border: 1px double rgb(128, 128, 128);
    /* background-color: rgb(214, 203, 214); */
}

.modal-text {
    font-family: 'Poppins', sans-serif;
    text-align: center;
    font-weight: bold;
}

.modal-footer {
    position: relative;
    top: 85px;
    right: 20px;
    border: none;
}

.cancelBtn {
    padding: 4px 10px;
    font-family: 'Poppins', sans-serif;
    background-color: rgb(28, 28, 247);
    color: white;
    letter-spacing: 1px;
}
.deleteBtn {
    padding: 4px 10px;
    font-family: 'Poppins', sans-serif;
    background-color: rgb(243, 26, 26);
    color: white;
    letter-spacing: 1px;
}



/* KeyFrames */

@-webkit-keyframes modalBox {
    from {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        opacity: 0;
    }
    to {
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        opacity: 1;
    }
    
}

@keyframes modalBox {
    from {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
        opacity: 0;
    }
    to {
        -webkit-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
        opacity: 1;
    }
    
}
